html, body {
  overflow-x: hidden;
  font-size: clamp(0.6rem, 1.5vh, .9rem );
  height: 100%;
  display: flex;
  margin: 0 auto;
}


.invoice {
  max-width: 210mm;
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: rgb(250, 250, 250);
}

.receiver-info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

}

.receiver-status {
  margin: 15px 30px 15px 15px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;

  min-width: 50px;
}

.receiver-status2 {
  margin: 15px 15px 15px 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  min-width: 50px;
}

p {
  margin: 0;
  font-family: Noto Sans KR;
  font-size: clamp(0.6rem, .7vw, .8rem );
  color: rgb(20, 20, 20, 70%);
  font-weight: 500;
}

h2 {
  text-align: right;
}

.logo {
  padding: 15px;
  flex-direction: column;
  display: flex;
  align-items: center;
  margin-bottom: 0%;
}

.footer {
  padding: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(250, 250, 250);
  max-width: 210mm;
}

.table-div {
  padding: 15px;
  flex: 1;
}

table {
  margin-top: 5vh;
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  padding: 50px;
  flex-wrap: wrap;
}

thead {
  font-family: 'Avenir Next Condensed Medium';
  color: lightgrey;
  font-size: clamp(0.6rem, 1vw, .8rem );
  border-bottom: 1px rgba(211, 211, 211, 0.788) solid;
}

thead th:nth-child(1) {
  width: 3%;
  text-align: center;
}


thead th:nth-child(2) {
  width: 10%;
  text-align: left;
  padding-left: 10px;
}

thead th:nth-child(3) {
  width: 4%;
  text-align: center;
}

thead th:nth-child(4) {
  width: 4%;
  text-align: center;
}

thead th:nth-child(5) {
  width: 4%;
  text-align: center;
}

thead th:nth-child(6) {
  width: 4%;
  text-align: center;
}




tbody td:nth-child(1) {
  width: 4%;
  text-align: center;
  
}

tbody td:nth-child(2) {
  width: 20%;
  text-align: left;
  padding-left: 10px;
  
}

tbody td:nth-child(4) {
  width: 4%;
  text-align: center;
  
}


th {
  margin: 0px 15px 0px 15px;
  /* border: 1px solid red; */
}

td {
  /* font-family: 'Apple LiGothic'; */
  margin: 0px 15px 0px 15px;
  height: 33px;
  text-align: center;
  font-family: 'Avenir Next Condensed';
  font-size: clamp(0.6rem, .7vw, .8rem );
  color: rgb(20, 20, 20, 70%);
  /* border: 1px solid red */
}























